<template>
  <ec-container height="100%" color-code="001" class="info" title="基地单元衔接审核" show-title>
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-query-panel :defaultFold="true" @query="onQuery" @reset="resetHandle">
            <ec-query-item label="基地单元名称">
              <a-input v-model.trim="query.baseCellName" placeholder="请输入基地单元名称" />
            </ec-query-item>
            <ec-query-item label="审核状态">
              <ec-select
                showSearch
                allowClear
                placeholder="请选择审核状态"
                v-model.trim="query.aduitState"
              >
                <a-select-option key="1">已审核</a-select-option>
                <a-select-option key="0">未审核</a-select-option>
              </ec-select>
            </ec-query-item>
          </ec-query-panel>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-body>
          <ec-v-layout :auto="false">
            <!-- 表格工具栏 -->
            <ec-v-item height="auto">
              <ec-panel-toolbar>
                <ec-panel-item-left>
                  <a-button type="primary" @click="onAgree">同意</a-button>
                  <a-button type="primary" ghost @click="onReturn">回退</a-button>
                </ec-panel-item-left>
                <ec-panel-item-right>
                  <div class="unit-box">
                    <div class="select">
                      重量单位：
                      <ec-select showSearch v-model.trim="weightUnit">
                        <a-select-option v-for="item in weightUnitList" :key="item.enumKey">
                          {{ item.name }}
                        </a-select-option>
                      </ec-select>
                    </div>
                    <div class="select">
                      面积单位：
                      <ec-select showSearch v-model.trim="areaUnit">
                        <a-select-option v-for="item in areaUnitList" :key="item.enumKey">
                          {{ item.name }}
                        </a-select-option>
                      </ec-select>
                    </div>
                  </div>
                </ec-panel-item-right>
              </ec-panel-toolbar>
            </ec-v-item>
            <!-- 列表 -->
            <ec-v-item>
              <ec-table
                ref="tableRef"
                :loading="loading"
                show-overflow
                height="100%"
                :auto="false"
                @page-action="queryHandle"
                :pagination="pagination"
                :data="tableData"
                @checkbox-all="onSelAllTableData"
                @checkbox-change="onSelTableData"
              >
                <ec-table-col type="checkbox" width="60" align="center"></ec-table-col>
                <ec-table-col type="seq" title="序号" width="50" align="center" />
                <ec-table-col
                  field="linkInduProvOrgName"
                  title="省级工业公司"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="linkInduOrgName"
                  title="对口工业企业"
                  min-width="150"
                  align="left"
                />
                <ec-table-col field="provOrgName" title="省公司" min-width="150" align="left" />
                <ec-table-col field="cityOrgName" title="市公司" min-width="150" align="left" />
                <ec-table-col field="provDivisionName" title="省" min-width="150" align="left" />
                <ec-table-col
                  field="cityDivisionName"
                  title="地(州、市)"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="counDivisionName"
                  title="县(区、市)"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="baseCellCode"
                  title="基地单元编码"
                  min-width="150"
                  align="left"
                />
                <ec-table-col
                  field="baseCellName"
                  title="基地单元名称"
                  min-width="150"
                  align="left"
                />
                <ec-table-col field="brandName" title="品牌" min-width="150" align="left" />
                <ec-table-col field="leafTypeCode" title="烟叶类型" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>
                      {{ row.leafTypeCode | getNameBy(leafTypeObj) }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col
                  field="leafVarietyName"
                  title="烟叶品种"
                  min-width="150"
                  align="left"
                />
                <ec-table-col field="fieldArea" title="基本烟田面积" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>{{ row.fieldArea | amountFormat(areaUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col field="plantArea" title="种植面积" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>{{ row.plantArea | amountFormat(areaUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col field="buyWeight" title="收购量" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>{{ row.buyWeight | amountFormat(weightUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col
                  field="techSuppOrg"
                  title="技术依托单位"
                  min-width="150"
                  align="left"
                />
                <ec-table-col field="unitProp" title="单元属性" min-width="150" align="left">
                  <template #default="{ row }">
                    <div>
                      {{
                        row.unitProp
                          | getNameBy(unitPropList, {
                            key: 'enumKey',
                            label: 'name',
                          })
                      }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col field="billState" title="状态" width="100" align="left">
                  <template #default="{ row }">
                    <div>
                      {{ row.billState | getNameBy(billStateObj) }}
                    </div>
                  </template>
                </ec-table-col>
              </ec-table>
            </ec-v-item>
          </ec-v-layout>
        </ec-panel-body>
      </ec-panel>
    </ec-container-body>
  </ec-container>
</template>

<script>
export default {
  name: 'PanelSupple',
  data() {
    return {
      loading: false,
      title: '',
      query: {}, // 查询数据
      // 重量单位
      weightUnitList: [],
      // 面积单位
      areaUnitList: [],
      // 单元属性
      unitPropList: [],
      // 烟叶类型列表
      leafTypeList: [],
      // 烟叶品种
      leafVarietyList: [],
      // 当前重量单位
      weightUnit: '500000',
      // 当前面积单位
      areaUnit: '10000',
      tableData: [], // 表格数据
      pagination: {
        // 分页
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      // 选中表格数据
      selTableData: [],
    };
  },
  computed: {
    leafTypeObj() {
      return this.leafTypeList.map((item) => {
        return {
          key: item.leafTypeCode,
          title: item.leafTypeName,
        };
      });
    },
    billStateObj() {
      return this.billStateList.map((item) => {
        return {
          key: item.enumKey,
          title: item.name,
        };
      });
    },
    busiYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.CENTER_YEAR;
    },
  },
  async created() {
    // 通过路由获取appCode
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM';
    this.query.busiYear = this.busiYear;

    this.handleGetEnum();
    this.handleQueryLeafTypeList();
  },
  methods: {
    // 查询枚举值
    handleGetEnum() {
      const params = {
        enumTypeCodes: ['WEIGHT_UNIT', 'AREA_UNIT', 'BASE_UNIT_NATURE', 'BILL_STATE'],
      };
      this.$apis.bim.enumType.query(params).then((res) => {
        const enumMap = res?.enumMap || {};
        this.weightUnitList = enumMap.WEIGHT_UNIT || [];
        this.areaUnitList = enumMap.AREA_UNIT || [];
        this.unitPropList = enumMap.BASE_UNIT_NATURE || [];
        this.billStateList = enumMap.BILL_STATE || [];
      });
    },
    // 查询烟叶类型
    handleQueryLeafTypeList() {
      this.$apis.bim.leafType.query({}).then((res) => {
        this.leafTypeList = res?.rows || [];
      });
    },
    // 重置
    resetHandle() {
      this.query = {
        busiYear: this.busiYear,
      };
      this.onQuery();
    },
    // 查询
    onQuery() {
      this.$refs.tableRef.pageAction(1, 10);
    },
    // 分页查询
    queryHandle(pageNumber, pageSize) {
      const params = {
        pageNo: pageNumber,
        pageSize,
        ...this.query,
      };
      this.loading = true;
      this.$apis.apm.baseAudit
        .query(params)
        .then((res) => {
          this.loading = false;
          this.tableData = res?.rows || [];
          this.pagination.total = res?.totalRows;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    // table选中对应项
    onSelTableData({ records }) {
      this.selTableData = records;
    },
    // table选中所有项
    onSelAllTableData({ records }) {
      this.selTableData = records;
    },
    // 同意
    onAgree() {
      if (!this.selTableData.length) {
        this.$message.error('请选择一条数据再操作');
        return;
      }

      const flag = this.selTableData.some((item) => item.billState === '6');
      if (flag) {
        this.$message.error('已审核的基地单元无法再操作,请重新选择！');
        return;
      }

      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认同意选中数据，确认请点击'确认'按钮，取消点击'取消'按钮
          </div>
        ),
        onOk: () => {
          const params = {
            bmUnitLinkRecIds: this.selTableData.map((item) => item.bmUnitLinkRecId),
            actionType: '6',
          };
          this.$apis.apm.baseAudit.agree(params).then(() => {
            this.$message.success(`同意成功`);
            this.onQuery();
          });
        },
      });
    },

    // 回退
    onReturn(type) {
      if (!this.selTableData.length) {
        this.$message.error('请选择一条数据再操作');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">是否回退选中数据，确认请点击'确认'按钮，取消点击'取消'按钮</div>
        ),
        onOk: () => {
          const params = {
            bmUnitLinkRecIds: this.selTableData.map((item) => item.bmUnitLinkRecId),
            actionType: type === '5',
          };
          this.$apis.apm.baseAudit.agree(params).then(() => {
            this.$message.success(`回退成功`);
            this.onQuery();
          });
        },
      });
    },
  },
};
</script>
<style lang="less" scoped>
.unit-box {
  display: flex;
  align-items: center;
  .select {
    margin-left: 20px;
    /deep/ .ant-select {
      width: 100px;
    }
  }
}
</style>
